import React from 'react';
import {
  Box,
  Typography,
  IconButton,
  Chip,
  Stack
} from '@mui/material';
import { Edit as EditIcon, Delete as DeleteIcon, ExpandMore as ExpandIcon, ExpandLess as CollapseIcon } from '@mui/icons-material';
import { Trade } from '../../types/trade';
import { TradeListItem, TradeInfo, TradeActions } from '../StyledComponents';
import { getTagChipStyles, formatTagForDisplay } from '../../utils/tagColors';
import { useTheme } from '@mui/material/styles';
import TradeDetailExpanded from '../TradeDetailExpanded';
import { set } from 'date-fns';

interface TradeListProps {
  trades: Trade[];
  expandedTradeId: string | null;
  onTradeClick: (tradeId: string) => void;
  onEditClick: (trade: Trade) => void;
  onDeleteClick: (tradeId: string) => void;
  onZoomedImage: (url: string) => void;
  onUpdateTradeProperty?: (tradeId: string, updateCallback: (trade: Trade) => Trade) => Promise<Trade | undefined>;
}

const TradeList: React.FC<TradeListProps> = ({
  trades,
  expandedTradeId,
  onTradeClick,
  onEditClick,
  onDeleteClick,
  onZoomedImage,
  onUpdateTradeProperty
}) => {
  const theme = useTheme();
  return (
    <Box sx={{ mt: 2 }}>
      <Typography variant="subtitle1" sx={{ mb: 1 }}>
        Trades
      </Typography>

      {trades.length === 0 ? (
        <Typography variant="body2" color="text.secondary" sx={{ fontStyle: 'italic' }}>
          No trades for this day
        </Typography>
      ) : (
        <Stack spacing={1}>
          {trades.map((trade) => (
            <React.Fragment key={trade.id}>
              <TradeListItem
                $type={trade.type}
                onClick={() => onTradeClick(trade.id)}
                sx={{
                  cursor: 'pointer',
                  ...(trade.isTemporary && {
                    opacity: 0.7,
                    border: '1px dashed',
                    borderColor: 'divider',
                    backgroundColor: theme => theme.palette.mode === 'dark' ? 'rgba(255, 255, 255, 0.05)' : 'rgba(0, 0, 0, 0.02)'
                  })
                }}
              >
                <TradeInfo>
                  <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 0.5, justifyContent: 'space-between' }}>
                    <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                      {trade.name && (
                        <Typography variant="body2" sx={{ fontWeight: 600 }}>
                          {trade.isTemporary ? trade.name : ` ${trade.name.replace(/^ /, '')}`}
                        </Typography>
                      )}
                      <Typography variant="body1" sx={{
                        fontWeight: 500,
                        color: trade.type === 'win'
                          ? 'success.main'
                          : trade.type === 'loss'
                            ? 'error.main'
                            : 'info.main'
                      }}>
                        ${Math.abs(trade.amount).toLocaleString()}
                      </Typography>
                    </Box>
                    <Box sx={{ mt: 0.5 }}>
                      {expandedTradeId === trade.id ?
                        <CollapseIcon fontSize="small" sx={{ color: 'text.secondary' }} /> :
                        <ExpandIcon fontSize="small" sx={{ color: 'text.secondary' }} />
                      }
                    </Box>
                  </Box>

                  {trade.tags && trade.tags.length > 0 && (
                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5, mt: 1 }}>
                      {trade.tags
                        .filter(tag => !tag.startsWith('Partials:'))
                        .map((tag, tagIndex) => (
                          <Chip
                            key={tagIndex}
                            label={formatTagForDisplay(tag)}
                            size="small"
                            sx={{
                              ...getTagChipStyles(tag, theme),
                              height: '20px',
                              '& .MuiChip-label': {
                                px: 1,
                                fontSize: '0.7rem'
                              }
                            }}
                          />
                        ))}
                    </Box>
                  )}
                </TradeInfo>

                <TradeActions>
                  <IconButton
                    size="small"
                    onClick={(e) => {
                      e.stopPropagation();
                      onEditClick(trade);
                    }}
                  >
                    <EditIcon fontSize="small" />
                  </IconButton>
                  <IconButton
                    size="small"
                    onClick={(e) => {
                      e.stopPropagation();
                      onDeleteClick(trade.id);
                    }}
                  >
                    <DeleteIcon fontSize="small" />
                  </IconButton>
                </TradeActions>
              </TradeListItem>

              {expandedTradeId === trade.id && (
                <TradeDetailExpanded
                  trade={trade}
                  isExpanded={true}
                  setZoomedImage={onZoomedImage}
                  onUpdateTradeProperty={onUpdateTradeProperty}
                />
              )}
            </React.Fragment>
          ))}
        </Stack>
      )}
    </Box>
  );
};

export default TradeList;
